<template>
  <view class="swiper-container">
    <swiper class="swiper-box" :style="swiperStyle" :circular="true" :autoplay="true">
      <swiper-item v-for="(item, index) in swiperList" :key="index" @click="clickSwiper(item)">
        <view class="swiper-item">
          <div class="title uv-line-1" v-if="showSwiperTitle">{{ item.name }}</div>
          <image :src="item.images_url" mode="aspectFill" />
        </view>
      </swiper-item>
    </swiper>
    <view v-if="showNoticeBar" class="noticebar">
      <noticebar :isSwiper="true" />
    </view>
  </view>
</template>

<script>
import { getWxRecommend } from '@/api/home/index'
import noticebar from '@/pages/tabbar/home/components/noticeBar/index.vue'
export default {
  components: {
    noticebar,
  },
  props: {
    dataConfig: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      noticeText: '',
      noticeList: [],
      swiperList: [],
    }
  },
  computed: {
    showSwiperTitle() {
      return this.dataConfig?.showSwiperTitle?.value == 1
    },
    showNoticeBar() {
      return this.dataConfig?.showNoticeBar?.value == 1
    },
    heightStyle() {
      return this.dataConfig?.height?.value || 442
    },
    themeConfig() {
      return this.$store.app.themeConfig
    },
    swiperStyle() {
      return {
        height: this.heightStyle + 'rpx',
      }
    },
  },
  methods: {
    async getWxRecommendList() {
      let res = await getWxRecommend({})
      if (res.code == 200) {
        this.swiperList = res.data
      }
    },
    clickSwiper(item) {
      const { topic_id, type_id, url } = item
      switch (type_id) {
        case 1:
          uni.navigateTo({
            url: `/pages-sub/notice/detail?notice_id=${topic_id}`,
          })
          break

        case 7:
          if (url) {
            window.location.href = url
          }
          break
        default:
          break
      }
    },
  },
  created() {
    this.getWxRecommendList()
  },
}
</script>

