  position: relative;
  width: 100%;
  box-sizing: border-box;
  overflow: hidden;
}

.noticebar {
  position: absolute;
  bottom: 24rpx;
  right: 0;
  left: 0;
  z-index: 33;
}

.swiper-item {
  position: relative;
  height: 100%;

  .title {
    position: absolute;
    bottom: 24rpx;
    left: 32rpx;
    z-index: 2;
    max-width: 90%;
    font-size: 28rpx;
    color: #fff;
  }

  img,
  image {
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
</style>
